
let state = {
	_id: '',					//id
	token: '',					//
	nickname: '',				//
	sex: '',					//
	headimgurl: '',				//
	province: '',				//
	city: '',					//
	country: '',				//

	contactInformation:{		//
        QQ:'',       			//qq
        wechat:'',   			//
	},
	
	realInformation:{			//
		state: 0,				//0 1 2
		name:'',				//
	},

	notify: [],					//
	notifyNumber: 0,			//
	notifyType: '',				//

	loading: true,				//
	isRemember: false,			//
};

let mutations = {
	setUser(state, newUser) {
		console.log(newUser);
		for (var key in newUser) {
			state[key] = newUser[key];
		}

		state.notifyNumber = 0;

		for( let i = 0; i < state.notify.length; i++ ) {
			if( state.notify[i].state == 0) {
				state.notifyNumber++;
			}
		}
	},

	setNotify(state, data) {
		state.notify = data;
		state.notifyNumber = 0;
		//
		
		for( let i = 0; i < data.length; i++ ) {
			if( data[i].state == 0) {
				state.notifyNumber++;
			}
		}

		// //0
		// notifyShow() {
		// 	return this.notifyNumber > 0 ? true : false;
		// }
		// console.log(state.notify);
	}
};

let actions = {


	//
	signOut({commit, state, rootState}) {
		//accessToken
		localStorage.removeItem('accessToken');
		state._id = '';
	},


	//
	loginSuccess({commit, state, rootState}) {
		rootState.socketClass.socket.on('loginSuccess', (data)=> {
			//accessTokenlocalStorage
			// localStorage.setItem('accessToken', data.accessToken);
			//api
			rootState.socketClass.socket.emit('authentication', data.accessToken);
			//
			rootState.loginDialog = false;
			//
			commit('setUser', data);
		});

	},

	//
	authenticationSuccess({commit, state, rootState}) {
		rootState.socketClass.socket.on('authenticationSuccess', (data)=> {
			//accessTokenlocalStorage
			localStorage.setItem('accessToken', data.accessToken);
			//
			commit('setUser', data);

			// //
			// rootState.socketClass.myEmit('getNotify', {ntype: state.notifyType})
			// .then((res)=> {
			// 	commit('setNotify', res);
			// })
			// .catch((err)=> {
			// 	console.log(err);
			// });
		});
	},

	//
	appError({commit, state, rootState}) {
		rootState.socketClass.socket.on('appError', (err)=> {

			rootState.errorSnackbar = {
				state: true,
				text: err
			}

		});
	},



//////////////////////////////////////////////////////////////////////////////////////////////////////


	//
	notify({commit, state, rootState}) {
		rootState.socketClass.socket.on('notify', ()=> {
			rootState.socketClass.myEmit('getNotify', {notifyType: 0,  currentPage: 1})
			.then((res)=> {
				commit('setNotify', res);
				rootState.infoSnackbar = {state: true, text: ''};
			})
			.catch((err)=> {
				console.log(err);
			});
		});
	},

	//
	getNotify({commit, state, rootState}, data) {
		return new Promise((resolve, reject)=> {

			rootState.socketClass.myEmit('getNotify', {notifyType: data.notifyType,   currentPage: data.currentPage})
			.then((res)=> {
				commit('setNotify', res);

				resolve();
			})
			.catch((err)=> {
				console.log(err);
			})
		});
	},

	//
	getNotifyReturn({commit, state, rootState}, data) {
		return new Promise((resolve, reject)=> {
			rootState.appLoading = true;
			rootState.socketClass.myEmit('getNotify', {notifyType: data.notifyType,  currentPage: data.currentPage})
			.then((res)=> {
				// commit('setNotify', res);
				rootState.appLoading = false;
				resolve(res);
			})
			.catch((err)=> {
				reject(err);
				rootState.appLoading = false;
			})
		});
	},

	//
	putNotify({commit, state, rootState}, data) {
		return new Promise((resolve, reject)=> {
			rootState.socketClass.myEmit('putNotify', {_id: data._id})
			.then((res)=> {
				resolve();
			});
		});
	},

	//
	deleteNotify({commit, state, rootState}, data) {
		return new Promise((resolve, reject)=> {
			rootState.socketClass.myEmit('deleteNotify', {_id: data._id})
			.then((res)=> {
				resolve();
			});
		});
	}


};

let getters = {

};


const user = {
	state,
	mutations,
	actions,
	getters
};

export default user;


